@charset "UTF-8";
/*doc
---
title: Tamogram Theme
category: index
---

Tamogram : 1 column style guide theme for [Hologram](http://trulia.github.io/hologram/).

<https://github.com/tam-matsuo/tamogram>
*/
/* global */
.styleguide_header,
.styleguide_content > :not(.styleguide_preview),
.styleguide_footer {
  font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, freesans, sans-serif;
  color: #222;
  line-height: 1.6;
  box-sizing: border-box; }

.styleguide_header a,
.styleguide_content > a,
.styleguide_content > :not(.styleguide_preview) a,
.styleguide_footer a {
  color: #00aeeb;
  text-decoration: none; }

.styleguide_header a:hover,
.styleguide_content > a:hover,
.styleguide_content > :not(.styleguide_preview) a:hover,
.styleguide_footer a:hover {
  text-decoration: underline; }

.styleguide_header ul,
.styleguide_content > ul,
.styleguide_content > :not(.styleguide_preview) ul,
.styleguide_footer ul {
  margin: 1em 1em 4em 1em; }

.styleguide_header dl,
.styleguide_content > dl,
.styleguide_content > :not(.styleguide_preview) dl,
.styleguide_footer dl {
  margin: 1em 1em 4em 1em; }

.styleguide_header ol,
.styleguide_content > ol,
.styleguide_content > :not(.styleguide_preview) ol,
.styleguide_footer ol {
  margin: 1em 1em 4em 1em; }

.styleguide_header li,
.styleguide_content > li,
.styleguide_content > :not(.styleguide_preview) li,
.styleguide_footer li {
  list-style-position: inside; }

.styleguide_header ul li,
.styleguide_content > ul li,
.styleguide_content > :not(.styleguide_preview) ul li,
.styleguide_footer ul li {
  list-style: disc; }

.styleguide_header ol li,
.styleguide_content > ol li,
.styleguide_content > :not(.styleguide_preview) ol li,
.styleguide_footer ol li {
  list-style: decimal; }

.styleguide_header em,
.styleguide_content > em,
.styleguide_content > :not(.styleguide_preview) em,
.styleguide_footer em {
  font-weight: normal;
  font-style: italic; }

.styleguide_header strong,
.styleguide_content > strong,
.styleguide_content > :not(.styleguide_preview) strong,
.styleguide_footer strong {
  font-weight: bold; }

.styleguide_header code,
.styleguide_content > code,
.styleguide_content > :not(.styleguide_preview) code,
.styleguide_footer code {
  font-family: Consolas, Monaco, monospace;
  background-color: #f8f8f8;
  padding: 2px 4px;
  border-radius: 4px;
  font-size: 12px; }

/* preview */
.styleguide_preview {
  border: 1px solid #ddd;
  padding: 20px;
  margin: 40px 0; }
  .styleguide_preview p {
    margin: .5em 0; }

.styleguide_codeblock pre {
  font-family: Consolas, Monaco, monospace;
  font-size: 12px;
  background-color: #f8f8f8;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  margin: 40px 0; }

/* header */
.styleguide_header {
  width: 100%;
  padding: 2em;
  overflow: hidden;
  border-bottom: 1px solid #ddd;
  line-height: 30px; }
  .styleguide_header h1.styleguide_title {
    font-size: 30px;
    float: left;
    margin: 0;
    border-bottom: none; }

/* main */
.styleguide_main {
  max-width: 1200px;
  margin: 40px auto; }

/* footer */
.styleguide_footer {
  border-top: 1px solid #ddd;
  width: 100%;
  padding: 2em;
  font-size: 12px;
  text-align: right; }

/*doc
---
title: h1
name: h1
category: heading
---

```html
<div class="styleguide_content">
  <h1>h1 テキスト</h1>
</div>
```
*/
.styleguide_header h1,
.styleguide_content > h1,
.styleguide_content > :not(.styleguide_preview) h1,
.styleguide_footer h1 {
  font-size: 30px;
  font-weight: bold;
  margin-top: 3em;
  margin-bottom: 1em;
  border-bottom: 1px solid #ddd; }

